<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-input placeholder="" v-model="queryInfo.query" style="width:300px" @input="searchOrder">
        <el-button slot="append" icon="el-icon-search" @click="searchOrder"></el-button>
      </el-input>
      <!--  -->
      <el-table :data="orderListData" style="width: 100%">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="order_number" label="" width="400px"></el-table-column>
        <el-table-column prop="order_price" label="" width="120px"></el-table-column>
        <el-table-column prop="order_pay" label="" width="120px">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.order_pay==='0'"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="120px"></el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">
            {{scope.row.create_time | formatTime}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template>
            <el-button type="primary" icon="el-icon-edit" @click="editOrder"></el-button>
            <el-tooltip class="item" effect="dark" content="" :enterable="false" placement="top">
              <el-button type="success" icon="el-icon-location" @click="siteInfoBtn"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="pageSizeChange" @current-change="pageCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[10, 15, 20, 30]" :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
    <!-- dialog-- -->
    <el-dialog title="" :visible.sync="editOrderVisible" @close="editOrderClose">
      <el-form label-width="100px" :model="editOrderForm" :rules="editOrderRules" ref="editOrderRef">
        <el-form-item label="/" prop="province">
          <el-cascader v-model="editOrderForm.province" :options="cityData" :props="cascaderProps" width="100%"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="editOrderForm.address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editOrderVisible = false"> </el-button>
        <el-button type="primary" @click="editOrderSure"> </el-button>
      </span>
    </el-dialog>
    <!-- dialog-- -->
    <el-dialog title="" :visible.sync="siteInfoVisible">
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in siteInfo" :key="index" :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata'
export default {
  data() {
    return {
      // ()
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: window.sessionStorage.getItem('orderPageSize')
          ? parseInt(window.sessionStorage.getItem('orderPageSize'))
          : 10,
      },
      total: 0, // 
      // 
      orderListData: [],

      // 
      cascaderProps: {
        value: 'value',
        label: 'label',
        children: 'children',
        expandTrigger: 'hover',
      },
      // dialog
      editOrderVisible: false,
      // dialog
      editOrderForm: {
        province: [], // 
        address: '', // 
      },
      // 
      cityData,
      // dialog
      editOrderRules: {
        province: [
          { required: true, message: '/', trigger: 'blur' },
        ],
        address: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },

      // dialog
      siteInfoVisible: false,
      // 
      siteInfo: [
        {
          time: '2018-05-10 09:39:00',
          ftime: '2018-05-10 09:39:00',
          context: ',,',
          location: '',
        },
        {
          time: '2018-05-10 08:23:00',
          ftime: '2018-05-10 08:23:00',
          context:
            '[]  95338',
          location: '',
        },
        {
          time: '2018-05-10 07:32:00',
          ftime: '2018-05-10 07:32:00',
          context: ' []',
          location: '',
        },
        {
          time: '2018-05-10 02:03:00',
          ftime: '2018-05-10 02:03:00',
          context:
            '[], []',
          location: '',
        },
        {
          time: '2018-05-09 23:05:00',
          ftime: '2018-05-09 23:05:00',
          context: ' []',
          location: '',
        },
        {
          time: '2018-05-09 21:21:00',
          ftime: '2018-05-09 21:21:00',
          context: '[], []',
          location: '',
        },
        {
          time: '2018-05-09 13:07:00',
          ftime: '2018-05-09 13:07:00',
          context: ' ',
          location: '',
        },
        {
          time: '2018-05-09 12:25:03',
          ftime: '2018-05-09 12:25:03',
          context: '',
          location: '',
        },
        {
          time: '2018-05-09 12:22:24',
          ftime: '2018-05-09 12:22:24',
          context: 'HLA',
          location: '',
        },
        {
          time: '2018-05-08 21:36:04',
          ftime: '2018-05-08 21:36:04',
          context: '',
          location: '',
        },
      ],
    }
  },
  methods: {
    // 
    async getOrderData() {
      let { data: res } = await this.$http.get('orders', {
        params: {
          query: this.queryInfo.query,
          pagenum: this.queryInfo.pagenum,
          pagesize: this.queryInfo.pagesize,
        },
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.orderListData = res.data.goods
      this.total = res.data.total
    },

    // 
    searchOrder() {
      this.getOrderData()
    },

    // --
    pageSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      window.sessionStorage.setItem('orderPageSize', newSize)
      this.getOrderData()
    },
    // --
    pageCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderData()
    },

    // 
    editOrder() {
      this.editOrderVisible = true
    },
    // 
    editOrderSure() {
      this.$refs.editOrderRef.validate((valid) => {
        if (!valid) return false
        this.editOrderVisible = false
      })
    },
    // dialog
    editOrderClose() {
      this.$refs.editOrderRef.resetFields()
    },

    // dialog
    async siteInfoBtn() {
      // 
      // let { data: res } = await this.$http.get('/kuaidi/SF1405054441208')
      // if (res.meta.status !== 200) return this.$message.error(res.meta.message)
      this.siteInfoVisible = true
    },
  },
  created() {
    this.getOrderData()
  },
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>